@import '~node_modules/modern-normalize/modern-normalize.css';

body {
    background-image: url(../images/wavey-fingerprint.svg);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    padding: 0;
    margin: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}
